@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html{scroll-behavior:smooth}body{margin:0;padding:0;box-sizing:border-box;background-color:#1d1b1b;font-family:"Do Hyeon", sans-serif}h1,h2,h3,h4,h5,h6,p,ul{margin:0;padding:0}a{text-decoration:none}ul,li{list-style:none}


.header{padding:40px 20px 0px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.header__img{width:80%}@media (min-width: 768px){.header__img{width:622px}}

.search-section{padding-top:60px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.search-section__form{color:white;font-size:25px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.search-section__form .form__label{padding-bottom:15px}.search-section__form .form__input{width:50%;height:40px;font-size:20px;background-color:#75ff6a;font-family:"Do Hyeon", sans-serif}

.characters-section{padding:40px 30px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.characters-section .response-text{font-size:20px;color:#75ff6a}.characters-section__list{display:grid;grid-template-columns:1fr;grid-gap:20px}@media (min-width: 800px){.characters-section__list{grid-template-columns:1fr 1fr;grid-gap:25px;gap:25px}}@media (min-width: 1100px){.characters-section__list{grid-template-columns:1fr 1fr 1fr}}@media (min-width: 1500px){.characters-section__list{grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px}}.characters-section__list .card{width:250px;padding-bottom:20px;text-align:center;border:solid #000 5px;background-image:radial-gradient(circle at 100% 0%, #ffd490 0, #ffc686 16.67%, #ffb579 33.33%, #ffa16a 50%, #f48d5c 66.67%, #ec7b53 83.33%, #e66d4f 100%)}.characters-section .card:hover{border:solid #75ff6a 10px;background-image:radial-gradient(circle at 100% 0%, #e3ff77 0, #c6ff75 16.67%, #a2ff71 33.33%, #74ff6a 50%, #2ff166 66.67%, #00e668 83.33%, #00df70 100%)}

.article{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;color:black}.article__img{border-bottom:solid black 5px}.article__name{font-size:21px;padding-top:15px}.article__species{font-size:18px;padding-top:5px}

.main-detail{padding-top:80px;-webkit-flex-direction:column;flex-direction:column;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.main-detail .home-link{font-size:20px;color:white;font-family:"Do Hyeon", sans-serif;padding-bottom:10px}.main-detail .article{padding-bottom:20px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;border:solid #000 5px;background-image:radial-gradient(circle at 100% 0%, #ffd490 0, #ffc686 16.67%, #ffb579 33.33%, #ffa16a 50%, #f48d5c 66.67%, #ec7b53 83.33%, #e66d4f 100%)}

.main-not-found{padding-top:80px;-webkit-flex-direction:column;flex-direction:column;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.main-not-found .home-link{font-size:20px;color:white;font-family:"Do Hyeon", sans-serif;padding-bottom:10px}.main-not-found .message{font-size:30px;color:#75ff6a}

